  margin: auto;
  margin-top: 50px;
  margin-bottom: 30px;

  .slider-item {
    margin: 0 10px;
  }
}

.dietitian-slider-photos {
  position: relative;

  .slider-item {
    width: (100% / 4);
    margin: auto;

    img {
      width: 100%;
    }
  }
}